.fondo__cuentas ion-content {
  --background: rgb(14, 56, 92);
  --background: linear-gradient(180deg, #0CA0B0 0%, rgba(58,34,128,1) 100%);
}
.box-perfiles {
  text-align: center;
  display: grid;
}
.box-perfiles img {
  border-radius: 0.9rem;
  box-shadow: 3px 5px 8px 1px rgb(7 41 70);
  -webkit-box-shadow: 3px 5px 8px 1px rgb(7 41 70);
  -moz-box-shadow: 3px 5px 8px 1px rgb(7 41 70);
  width: 60px;
  max-width: 90px;
  margin: auto;
}
